<template>
  <div class="green-recommendation">
    <h1 class="title">绿色推荐</h1>
    <div v-if="loading" class="loading">加载中...</div>
    <div v-else-if="error" class="error">
      <p>获取数据时出错：{{ error }}</p>
    </div>
    <table v-else class="product-table">
      <thead>
        <tr>
          <th>名称</th>
          <th>描述</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="product in greenProducts" :key="product.id">
          <td>{{ product.name }}</td>
          <td>{{ product.description }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<style scoped>
.green-recommendation {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.title {
  color: #2e7d32;
  text-align: center;
  margin-bottom: 20px;
}

.loading {
  color: #666;
  text-align: center;
  font-style: italic;
}

.error {
  color: #d32f2f;
  text-align: center;
  font-weight: bold;
}

.product-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  overflow-x: auto;
  display: block;
}

@media (max-width: 480px) {
  .product-table th,
  .product-table td {
    padding: 8px;
    font-size: 0.9rem;
  }
}
.product-table th,
.product-table td {
  padding: 12px 15px;
  border-bottom: 1px solid #ddd;
  text-align: left;
}

.product-table th {
  background-color: #e8f5e9;
  color: #2e7d32;
}

.product-table tr:hover {
  background-color: #f1f8e9;
}
</style>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      greenProducts: [],
      loading: true,
      error: null
    };
  },
  mounted() {
    this.fetchGreenProducts();
  },
  methods: {
    fetchGreenProducts() {
      axios.get('/api/green-products')
        .then(response => {
          this.greenProducts = response.data;
        })
        .catch(error => {
          this.error = error.message;
        })
        .finally(() => {
          this.loading = false;
        });
    }
  }
};
</script>